import React from "react"
import { documentToReactComponents } from "@contentful/rich-text-react-renderer"
import { useStaticQuery, Link, graphql } from "gatsby"
import "../styles/index.css"

const TeamDetail = ({ data }) => {
  const other = useStaticQuery(graphql`
    query {
      allContentfulTeams(limit: 5) {
        edges {
          node {
            title
            name
            image {
              file {
                url
              }
            }
          }
        }
      }
    }
  `)
  const options = {
    renderNode: {
      "embedded-asset-block": node => {
        const alt = node.data.target.fields.title["en-US"]
        const url = node.data.target.fields.file["en-US"].url
        return (
          <div style={{ width: "20%", textAlign: "center" }}>
            <img alt={alt} src={url} />
          </div>
        )
      },
    },
  }
  return (
    <div className="sidebar-page">
      <div className="auto-container">
        <div className="row clearfix">
          <div className="col-lg-9 col-md-8 col-sm-6 col-xs-12">
            <section className="blog-container blog-detail">
              <div className="blog-post">
                <div
                  className="featured-blog-post wow fadeInLeft"
                  data-wow-delay="0ms"
                  data-wow-duration="1500ms"
                >
                  <article className="inner-box">
                    <div className="image mb-4">
                      {data.image && (
                        <img src={data.image.file.url} alt={data.image.title} />
                      )}
                    </div>
                    <div className="post-lower">
                      <div className="mt-4">
                        <h3 className="">{data.title}</h3>
                      </div>
                      <div className="post-desc">
                        {data.contant &&
                          documentToReactComponents(data.contant.json, options)}
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </section>
          </div>

          <div className="col-lg-3 col-md-4 col-sm-6 col-xs-12">
            <div className="sidebar">
              <div
                className="widget recent-posts wow fadeInUp"
                data-wow-delay="0ms"
                data-wow-duration="1500ms"
              >
                <div className="sidebar-title">
                  <h3>Other Teams</h3>
                </div>
                {other.allContentfulTeams.edges.map(edge => {
                  return (
                    <div className="post" key={edge.node.title}>
                      <div className="post-thumb">
                        {" "}
                        <img
                          src={edge.node.image.file.url}
                          alt={edge.node.image.title}
                        />{" "}
                      </div>
                      <h4>
                        <Link
                          to={edge.node.name}
                          style={{
                            boxShadow: `none`,
                            textDecoration: `none`,
                            color: `inherit`,
                          }}
                        >
                          {edge.node.title}
                        </Link>
                      </h4>
                    </div>
                  )
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default TeamDetail
