import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import Head from "../components/Head"
import "../styles/bootstrap.min.css"
import TeamDetail from "../pages/teamDetail"

export const query = graphql`
  query($name: String) {
    contentfulTeams(name: { eq: $name }) {
      title
      image {
        title
        file {
          url
        }
      }
      contant {
        json
      }
    }
  }
`
const Team = props => {
  return (
    <Layout>
      <Head title={props.data.contentfulTeams.title} />
      <TeamDetail data={props.data.contentfulTeams} />
    </Layout>
  )
}

export default Team
